import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteDataType } from '@core/services/route.service';
import { AdDetailPage } from '@feature/ad-detail/pages/ad-detail/ad-detail.page';
import { AdDetailResolver } from '@feature/ad-detail/resolvers/ad-detail.resolver';

const routes: Routes = [
	{
		path: ':adId/:title',
		component: AdDetailPage,
		resolve: { ad: AdDetailResolver },
		data: {
			[RouteDataType.IS_HEADER_VISIBLE]: false,
			[RouteDataType.IS_MOBILE_HEADER_VISIBLE]: false,
			[RouteDataType.IS_FOOTER_VISIBLE]: false,
			[RouteDataType.IS_PUBLIC_PAGE]: true,
		},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class PublicAdDetailPageRoutingModule {}
